

#container{
  background: #333;
  position: relative;
  width: 1000px;
  height: 1000px;
}
.bubble{
  position: absolute;
  background-image: url('./bubble.png');
  background-size: 100%;
  animation:myfirst 5s ease-in-out infinite alternate;
  
}

.bubble .img{
  width: 80%;
  height: 80%;
  margin: 10% auto;
  display: block;
  overflow: hidden;
  border-radius: 50%;
  object-fit: contain;
}

@keyframes myfirst
{
	from {
    transform: scale(0.8);
  }
	to {
    transform: scale(1.5)
  }
}

.pop-img{
  width: 50vw;
  height: 50vh;
  object-fit: contain;
}

.delete-icon{
  position: absolute;
  right: -40px;
  top: -40px;
  width: 40px;
  height: 40px;
  background: #fff;
  border-radius: 50%;
  font-size: 24px;
  line-height: 40px;
  text-align: center;
}